  padding-right: 20px; }
  .form__input, .form__textarea {
    display: block;
    width: 100%;
    padding: 10px 15px;
    border: 1px solid #DDDDDD;
    border-radius: 3px; }
  .form__textarea {
    min-height: 110px; }
  .form__row {
    display: flex;
    flex-wrap: wrap;
    margin-left: -20px;
    margin-right: -20px; }
  .form__input-wrapper {
    flex-grow: 1;
    flex-shrink: 0;
    flex-basis: 0;
    margin-top: 20px;
    padding-left: 20px;
    padding-right: 20px;
    min-width: 200px; }
    .form__input-wrapper:last-child {
      margin-right: 0; }
    .form__input-wrapper.error .form__input, .form__input-wrapper.success .form__input {
      padding-right: 50px; }
    .form__input-wrapper.success .form__input {
      border-color: #3D763D; }
    .form__input-wrapper.success .form__label {
      color: #3D763D; }
    .form__input-wrapper.success .form__input-holder:after {
      background-image: url("../imgs/checked.svg");
      background-size: 16px; }
    .form__input-wrapper.error .form__input {
      border-color: #A94442; }
    .form__input-wrapper.error .form__label {
      color: #A94442; }
    .form__input-wrapper.error .form__input-holder:after {
      background-image: url("../imgs/cross.svg");
      background-size: 12px; }
  .form__input-holder {
    position: relative; }
    .form__input-holder:after {
      content: '';
      position: absolute;
      z-index: 2;
      top: 10px;
      right: 15px;
      width: 20px;
      height: 20px;
      background-position: center;
      background-repeat: no-repeat; }
    .form__input-holder--arrowed:after, .form__input-holder--arrowed:before {
      content: '';
      position: absolute;
      display: block;
      right: 8px;
      width: 0;
      height: 0; }
    .form__input-holder--arrowed:after {
