body, html {
  min-height: 100%;
  font-size: 16px;
  -webkit-font-smoothing: antialiased; }

* {
  box-sizing: border-box; }

.hero-wrapper {
  flex-grow: 1;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh; }

.wrapper {
  max-width: 1180px;
  width: 100%;
  margin: 0 auto; }

.tabs__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
  border-bottom: 1px solid #DDDDDD; }

@media (max-width: 599px) {
  .tabs__item {
    width: 100%;
    text-align: center; } }

.tabs__item.disabled .tabs__link {
  pointer-events: none;
  opacity: 0.5; }

.tabs__link {
  text-decoration: none;
  line-height: normal;
  position: relative;
  bottom: -1px;
  display: block;
  width: 100%;
  padding: 15px 20px;
  border: 1px solid transparent;
  color: #337AB7; }
  .active .tabs__link {
    border-top: 1px solid #DDDDDD;
    border-left: 1px solid #DDDDDD;
    border-right: 1px solid #DDDDDD;
    border-bottom: 1px solid white;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    color: #000; }

.label {
  display: inline-block;
  margin-bottom: 8px;
  font-weight: bold; }

.radio {
  display: block;
  margin-bottom: 15px;
  cursor: pointer; }
  .radio__text {
    margin-left: 5px; }

.radio-area {
  margin-bottom: 25px; }

.form-section + .form-section {
  margin-top: 15px; }

.dnone {
  display: none; }
